@media screen and (min-width: 480px) {
  .test {
    width: 320px;
    padding-top: 22px;
    margin: 0 auto;
    min-height: 1590px;
    /* height: 100vh; */
  }

  .wrap_link {
    display: flex;
    align-items: center;
  }

  .link {
    text-decoration: none;
    color: #52555f;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
  }



  .link_text {
    visibility: hidden;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.04em;
    color: #52555f;
    margin-left: 8px;
    box-shadow: 5px 10px 20px rgba(175, 180, 191, 0.201);

  }

  .link:hover {
    transform: translatey(1px);
    color: #ff751d;
  }

  .link_text:hover {
    color: #ff751d;
  }

  .dateSwiperContainer {
    text-align: center;
    margin-top: 22px;
    width: fit-content;
    margin: 0 auto;
  }

  .text {
    font-family: 'Roboto';
    font-weight: 400;
    font-size: 12px;
    line-height: 1.16;
    letter-spacing: 0.04em;
    color: rgba(82, 85, 95, 0.7);
    margin-bottom: 5px;
  }

  .calendar {
    display: flex;
    justify-content: center;
  }

  .calendar_text {
    font-family: 'Roboto';
    font-weight: 700;
    font-size: 14px;
    line-height: 1.14;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    text-align: center;
    color: #000000;
    font-style: normal;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .btn {
    position: relative;
    background-color: transparent;
    width: 25px;
    height: 25px;
    padding: 0;
  }

  .btn:hover,
  .btn:focus {
    background-color: transparent;
    color: #f9680e;
  }

  .arrowBtn {
    position: absolute;
    font-size: 25px;
    bottom: 0;
    left: 0;
    color: #f76002;
  }

  .balance {
    text-align: center;
    margin-top: 32px;
  }

  .balance_text {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.16;
    letter-spacing: 0.02em;
    color: rgba(82, 85, 95, 0.7);
    margin-bottom: 8px;
  }

  .balance_cash {
    max-width: 183px;
    height: 14px;
    margin: 0 auto;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.16;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    padding: 15px 30px 15px 30px;
    border: 2px solid #ffffff;
    border-radius: 22px;
    margin-bottom: 32px;
    display: flex;
    align-items: center;
    justify-content: center;

  }

  .cash {
    max-width: 280px;
    height: 85px;
    margin: 0 auto;
    background: #ffffff;
    box-shadow: 5px 10px 20px rgba(170, 178, 197, 0.4);
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 32px;
  }

  .expenses {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 120px;

    padding: 10px;
  }

  .expenses__text {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.14;
    color: #52555f;
    margin-bottom: 6px;
  }

  .expenses__cash {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.14;
    letter-spacing: 0.04em;
    color: #e53935;
  }

  .span {
    border: 1px solid #e0e5eb;
    height: 70px;
  }

  .income {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 120px;
    padding: 10px;
  }

  .income__text {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.14;
    color: #52555f;
    margin-bottom: 6px;
  }

  .income__cash {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.14;
    letter-spacing: 0.04em;
    color: #407946;
  }
}

.isLoader {
