@media screen and (min-width: 768px) {
  .test {
    padding-top: 40px;
    padding-bottom: 80px;
    display: flex;
    justify-content: center;
    width: 704px;
    min-height: 920px;
  }

  .wrap {
    display: flex;
    width: 704px;
    margin: 0 auto;
    margin-bottom: 32px;
  }

  .wrap_link {
    display: flex;
    align-items: center;
  }

  .link_text {

    visibility: inherit;
  }




  .svg {
    display: flex;
    align-items: center;
  }

  .dateSwiperContainer {
    order: 3;
    margin-top: 0;
    margin-right: 1px;
  }

  .balance {
    order: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 130px;
    margin-top: 0;
  }

  .balance_text {
    margin-right: 20px;
    margin-bottom: 0;
  }

  .balance_cash {
    padding: 12px 20px;
    min-width: 125px;
    height: 44px;
    margin-bottom: 0;
  }

  .cash {
    max-width: 704px;
    height: 50px;
  }

  .expenses {
    flex-direction: row;
  }

  .expenses__text {
    margin-right: 15px;
    margin-bottom: 0;
  }

  .span {
    height: 36px;
    margin-right: 20px;
    margin-left: 20px;
  }

  .income {
    flex-direction: row;
  }

  .income__text {
    margin-right: 15px;
    margin-bottom: 0;
  }
}

